import React from 'react'

const Note = ({ note, toggleImportance, deleteNote }) => {
  const label = note.important
    ? 'make not important' : 'make important'

  return (
    <li className='float-start'>
      <div className="input-group d-flex flex-row justify-content-center align-items-center">
        <div className='w-50'>{note.content}</div>
        <div className='w-50'>
          <button className="btn btn-outline-secondary" type="button" onClick={toggleImportance}>{label}</button>
          <button className="btn btn-outline-secondary" type="button" onClick={deleteNote}>x</button>
        </div>
      </div>
      {/* <button onClick={toggleImportance}>{label}</button>
      <button onClick={toggleImportance}>x</button> */}
    </li>
  )
}

export default Note