import React, {useState, useEffect} from 'react'
import Note from './components/Note'
import Notification from './components/Notification'
import Footer from './components/Footer'
import noteService from './services/notes'
import "bootstrap/dist/css/bootstrap.min.css"
import '@popperjs/core'
import 'bootstrap/dist/js/bootstrap.bundle.min'

const App = () => {
  const [notes, setNotes] = useState([])
  const [newNote, setNewNote] = useState('')
  const [showAll, setShowAll] = useState(true)
  const [errorMessage, setErrorMessage] = useState(null)

  useEffect(() => {
    noteService
    .getAll()
    .then(initialNotes => {

      setNotes(initialNotes)
    })
  }, [])

  const addNote = (event) => {
    event.preventDefault()
   
    const noteObject = {
      content: newNote,
      date: new Date().toISOString(),
      important: Math.random() < 0.5,
      id: notes.length + 1,
    }

    noteService
      .create(noteObject)
      .then(returnedNote => {
        setNotes(notes.concat(returnedNote))
        setNewNote('')
      })
  }

  const handleNoteChange = (event) => {
    console.log(event.target.value)
    setNewNote(event.target.value)
  }

  const toggleImportanceOf = id => {
    const note = notes.find(n => n.id === id)
    const changedNote = { ...note, important: !note.important }
  
    noteService
      .update(id, changedNote).then(returnedNote => {
        setNotes(notes.map(note => note.id !== id ? note : returnedNote))
      })
      .catch(error => {
        setErrorMessage(
          `Note '${note.content}' was already removed from server`
        )
        setTimeout(() => {
          setErrorMessage(null)
        }, 5000)
        setNotes(notes.filter(n => n.id !== id))
      })
  }

  const deleteNote = id => {
    noteService
      .deleteNote(id)
      .then(newNotes => {
        console.log(typeof(newNotes))
        console.log(newNotes)
        setNotes(newNotes)
      })
      .catch(error => {
        setErrorMessage(error)
        setTimeout(() => {
          setErrorMessage(null)
        }, 5000)
      })
  }

  const notesToShow = showAll
    ? notes
    : notes.filter(note => note.important)

  return (
    <div className='d-flex flex-row justify-content-center align-items-center'>
      <div className='col-6 d-flex flex-column justify-content-center align-items-center mt-3 border rounded bg-light mx-5'>
        <h1 className='my-2'>Notes</h1>
        <Notification message={errorMessage} />
        <form onSubmit={addNote}>
          <div class="input-group mb-3">
            <input type="text" class="form-control" placeholder="note content..." aria-label="Recipient's username" aria-describedby="button-addon2"
                  value={newNote} onChange={handleNoteChange} />
            <button class="btn btn-outline-secondary" type="submit" id="button-addon2">Save</button>
          </div>
        </form> 
        <div>
          <button className='btn border rounded bg-warning my-4' onClick={() => setShowAll(!showAll)}>
            show {showAll ? 'important' : 'all' }
          </button>
        </div>
        <ul>
          {notesToShow.map(note =>
            <Note key={note.id}
                  note={note} 
                  toggleImportance={() => toggleImportanceOf(note.id)} 
                  deleteNote={() => deleteNote(note.id)} />
          )}
        </ul>
        
        <Footer />
      </div>
    </div>
  )
}

export default App